<template>
  <div class="player-wrapper">
    <section class="player" flex="cross:center">
      <section flex-item="2" class="player-basic">
        <section flex="cross:center">
          <i class="player-icon player-prev-icon" @click="handleSetSongIdx(parseInt(songIdx) - 1)"></i>
          <i class="player-icon" :class="[playStatus === false ? 'player-play-icon' : 'player-pause-icon']" @click="handleSetPlayStatus(!playStatus)"></i>
          <i class="player-icon player-end-icon" @click="handleSetSongIdx(parseInt(songIdx) + 1)"></i>
        </section>
      </section>
      <section flex-item="6" class="player-progress">
        <section class="bar-time">{{ parseInt(nowDuration / 60) }} : {{ parseInt(nowDuration % 60) }} / {{ parseInt(duration / 60) }} : {{ parseInt(duration % 60) }}</section>
        <section class="bar-total" ref="barTotal">
          <section class="bar-real" ref="barReal">
            <i class="bar-dot" @mousedown="mouseDown" @touchsStart.prevent="mouseDown" ref="barDot"></i>
          </section>
        </section>
      </section>
      <section flex-item="2" class="player-special">
        <section flex="cross:center">
          <!-- <i class="player-icon player-loop-icon"></i> -->
          <router-link class="player-icon player-comment-icon" v-if="nowDuration !== 0" :to="'/song/comment?id=' + songId" tag="i"></router-link>
          <i class="player-icon player-love-icon" v-if="userToken && nowDuration !== 0 && pagePath !== '/song/love'" @click="loveMusic"></i>
          <i class="player-icon player-delove-icon" v-if="userToken && nowDuration !== 0 && pagePath === '/song/love'" @click="deloveMusic"></i>
        </section>
      </section>
      <section flex-item="2" class="player-voice"></section>
    </section>
    <audio :src="'http://admin.music.com' + song" ref="audio" @timeupdate="audioProcess" @canplay="getDuration" @ended="playEnd"></audio>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'm1-footer',
  data () {
    return {
      dotWidth: 10,
      musicSrc: '',
      move: {
        status: false,
        startX: 0,
        left: 0
      },
      duration: 0,
      nowDuration: 0,
      pathName: ''
    }
  },
  created () {
    this.pathName = this.$route.name
  },
  computed: {
    ...mapState('player/song', [
      'songIdx', 'song', 'playStatus', 'pagePath', 'songId'
    ]),
    ...mapState('player/user', [
      'userToken'
    ])
  },
  mounted () {
    this.$nextTick(() => {
      this.mouseBinds()
    })
  },
  beforeDestroy () {
    this.mouseUnbinds()
  },
  methods: {
    mouseBinds () {
      // 绑定鼠标滑动事件
      document.addEventListener('mousemove', this.mouseMove)
      // 绑定鼠标松开事件
      document.addEventListener('mouseup', this.mouseUp)
    },
    mouseUnbinds () {
      // 解绑鼠标滑动事件
      document.removeEventListener('mousemove', this.mouseMove)
      // 解绑鼠标松开事件
      document.removeEventListener('mouseup', this.mouseUp)
    },
    // 鼠标按下事件
    mouseDown (e) {
      this.move.status = true
      this.move.startX = e.clientX
      this.move.left = this.$refs.barReal.clientWidth
    },
    // 鼠标滑动事件
    mouseMove (e) {
      if (!this.move.status) {
        return false
      }
      // 滑动坐标
      const endX = e.clientX
      // 滑动距离
      const dist = endX - this.move.startX
      // 计算实时进度条宽度 约束起点 - 终点范围
      const offsetWidth = Math.min(this.$refs.barTotal.clientWidth, Math.max(0, this.move.left + dist))
      this.moveSilde(offsetWidth)
    },
    // 鼠标松开事件
    mouseUp (e) {
      if (this.move.status === true) {
        e.stopPropagation()
        this.$refs.audio.currentTime = this.$refs.barReal.clientWidth / this.$refs.barTotal.clientWidth * this.duration
        if (this.$refs.audio.currentTim !== this.duration && this.playStatus === false) {
          this.handleSetPlayStatus(true)
        }
        this.move.status = false
      }
    },
    // 获取总时间
    getDuration () {
      this.duration = this.$refs.audio.duration
    },
    // 播放进度
    audioProcess () {
      if (!this.move.status) {
        this.nowDuration = this.$refs.audio.currentTime
        this.moveSilde(this.$refs.audio.currentTime / this.duration * this.$refs.barTotal.clientWidth)
      }
    },
    // 播放结束
    playEnd () {
      this.handleSetPlayStatus(false)
    },
    // 实施进度条变动
    moveSilde (offsetWidth, type) {
      this.$refs.barReal.style.width = `${offsetWidth}px`
    },
    // 收藏音乐
    async loveMusic () {
      const params = {
        ids: this.songId
      }
      // eslint-disable-next-line
      const res = await this.$http.post('/user/song/love', params)
      location.reload()
    },
    // 删除音乐
    async deloveMusic () {
      const params = {
        songId: this.songId
      }
      // eslint-disable-next-line
      const res = await this.$http.post('/user/song/dellove', params)
      // location.reload()
    },
    // 播放
    async played () {
      const params = {
        songId: this.songId
      }
      // eslint-disable-next-line
      const res = await this.$http.post('/index/song/played', params)
    },
    // 评论列表
    ...mapActions('player/song', [
      'handleSetPlayStatus', 'handleSetSongIdx'
    ])
  },
  watch: {
    playStatus (val) {
      if (val) {
        this.$nextTick(() => {
          this.$refs.audio.play()
          this.played()
        })
      } else {
        this.$refs.audio.pause()
      }
    },
    songIdx (val) {
      if (typeof val !== 'undefined') {
        this.$refs.audio.load()
        this.$nextTick(() => {
          this.handleSetPlayStatus(true)
          this.$refs.audio.play()
          this.played()
        })
      }
    },
    '$route' (to, from) {
      this.pathName = to.name
    }
  }
}
</script>

<style scoped lang="scss">
  .player-wrapper {
    padding: 0 7.638889%;
    width: 100%;
    .player{
      height: 80px;
      .bar-time {
        padding: 0 0 10px;
        color: #bbb;
        text-align: right;
      }
      .bar-total {
        position: relative;
        width: 100%;
        height: 2px;
        background: #999;
        .bar-real {
          position: relative;
          width: 0;
          height: 2px;
          background: #fff;
          cursor: pointer;
        }
        .bar-dot {
          position: absolute;
          top: 50%;
          right: -5px;
          transform: translateY(-50%);
          display: inline-block;
          width: 10px;
          height: 10px;
          background: #fff;
          border-radius: 50%;
        }
      }
    }
    .player-icon {
      display: block;
      background-image: url(../../assets/images/player.png);
      cursor: pointer;
      &.player-prev-icon {
        width: 19px;
        min-width: 19px;
        height: 20px;
        background-position: 0 -30px;
      }
      &.player-play-icon {
        width: 21px;
        min-width: 21px;
        height: 29px;
        margin: 0 50px;
        background-position: 0 0;
      }
      &.player-pause-icon {
        width: 21px;
        min-width: 21px;
        height: 29px;
        margin: 0 50px;
        background-position: -30px 0;
      }
      &.player-end-icon {
        width: 19px;
        min-width: 19px;
        height: 20px;
        background-position: 0 -52px;
      }
      &.player-loop-icon {
        width: 26px;
        height: 25px;
        margin-left: 20px;
        background-position: 0 -205px;
      }
      &.player-comment-icon {
        width: 24px;
        height: 24px;
        margin-left: 20px;
        background-position: 0 -400px;
      }
      &.player-love-icon {
        width: 24px;
        height: 24px;
        margin-left: 20px;
        background-position: 157px -614px;
      }
      &.player-delove-icon {
        width: 24px;
        height: 24px;
        margin-left: 20px;
        background-position: 48px -94px;
      }
    }
  }
</style>
